<template>
    <div class="row">
        <div class="col-lg-3 col-md-4 col-ms-6 col-xs-6  mt" v-for="(item,index) in productData">
            <div class="grid-item">
                <img :src="'../static/object/'+item.src" :title="item.title"/>
                <h4>{{item.title}}</h4>
                <div class="overlay">
                    <div class="content">
                        <a :href="item.url" target="_blank">
                            <p class="description">{{item.describe | substrFun(36)}}</p>
                            <!-- <p class="link">查看详情</p> -->
                        </a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "WorkDetail",
        data(){
            return{
            }
        },
		filters:{
		    substrFun(value,num){
		         // console.log(value,num);
				 
		        if(value.length>=num)
		        {
		            return value.substr(0,num)+'...'
		        }else{
					return value
				}
		    }
		},
        props:{
            productData: {
                type: Array,
                default: function () {
                    return[
                        {
                            title: "四宗最",
                            src:"sizongzui.jpg",
                            url: "http://qd.house.163.com/special/021014J8/qdsqszz.html",
							describe:"2015青岛商圈盘点四宗最_网易房产独家"
                        }
                    ]
                }
            }
        }
    }
</script>

<style scoped>

li{
    display: inline-block;
}
.mt{
    margin-bottom: 1.786rem;
}
.grid-item {
    overflow: hidden;
    cursor: default;
}
h4{
    text-align: center;
    padding-top: 1.071rem;
    line-height: 2.5rem;
}
.grid-item > .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    -webkit-transform: scale(0.1);
    -ms-transform: scale(0.1);
    transform: scale(0.1);
    opacity: 0;
    -moz-transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

.grid-item:hover > .overlay {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
    opacity: 1;
}

.grid-item > .overlay::after {
    content: '';
    position: absolute;
    top: 9px;
    left: 9px;
    right: 9px;
    bottom: 9px;
    border: 1px solid #57c654;
}

.grid-item > .overlay > .content {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    width: 100%;
    padding: 1.5625rem;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
	text-indent:0em;
    z-index: 2;
}
.grid-item > .overlay > .content > a:hover{
	text-decoration: none;
}
.grid-item > .overlay > .content > a >.description {
    color: #57c654;
    font-size: 1.23rem;
    text-transform: uppercase;
	text-align: left;
}

.grid-item > .overlay > .content > a >.link {
    color: #b5e8c1;
	text-align: left;
	text-indent: 0em
}

.grid-item > img {
    display: block;
    width: 100%;
    -moz-transition: -moz-transform 0.3s ease-in-out;
    -webkit-transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    /* border-radius: 3%; */
}

.grid-item:hover img {
    -ms-transform: scale(1.2); /* IE 9 */
    -webkit-transform: scale(1.2); /* Safari */
    transform: scale(1.2);
}

</style>



